.user-cell-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 110px;
  height: 40px;
  align-items: center;
  margin: 20px 0;
  width: 85%;
}
.user-details-container {
  display: flex;
}

.user-cell-id,
.user-cell-name,
.user-cell-email,
.user-cell-phone {
  color: white;
  padding: 0 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 40px;
  border-radius: 8px;
  margin-right: 40px;
  background-color: rgba(0, 0, 0, 0.397);
}
.user-cell-id {
  width: 100px;
}

.user-cell-name {
  width: 355px;
}
.user-cell-email {
  width: 250px;
}
.user-cell-phone {
  width: 200px;
}
.user-cell-btn-container {
  display: flex;
}

.user-cell-btn {
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 90px;
  height: 40px;
  border-radius: 8px;
  outline: none;
  cursor: pointer;
  border: none;
  margin-right: 40px;
}
.view-user-btn {
  background-color: rgba(0, 0, 255, 0.801);
}
.edit-user-btn {
  background-color: rgba(0, 128, 0, 0.801);
}
.delete-user-btn {
  background-color: rgba(250, 6, 6, 0.801);
}
.link-user {
  text-decoration: none;
}
