.edit-section-container {
  display: flex;
  margin: auto;
  margin-top: 40px;
  width: 85%;
  height: max-content;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.425);
  padding: 0 70px;
  border: none;
  justify-content: center;
}
.user-form {
  border-radius: 8px;
  font-size: 18px;
  width: 50%;
  color: white;
  margin: 70px 0;
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.644);
  height: max-content;
  padding: 20px 20px;
}
.submit-btn {
  border-radius: 8px;
  cursor: pointer;
  background-color: rgb(92, 255, 16);
}
.user-form > input {
  font-size: 18px;
  height: 40px;
  border: none;
  outline: none;
  margin: 10px 0;
  padding: 0 10px;
}
