.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  margin-top: 40px;
  width: 85%;
  height: 120px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.425);
  padding: 0 70px;
  border: none;
}
.main-head {
  font-size: 60px;
  color: rgb(214, 214, 214);
}
.link {
  color: white;
  text-decoration: none;
}
