.user-table-container {
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-top: 40px;
  width: 85%;
  height: max-content;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.425);
  padding: 0 70px;
  border: none;
}
.table-headers {
  color: rgba(255, 255, 255, 0.822);
  display: flex;
  margin-top: 20px;
}
.user-id-header {
  width: 140px;
}

.user-name-header {
  width: 395px;
}
.user-email-header {
  width: 290px;
}
.user-phone-header {
  width: 200px;
}
